import server from '../../server.js';
export default {
	data() {
		return {
			imgurl:{
				man:"../../static/11.png",
				woman:"../../static/21.png"
			},
			dataSex:1,
			name:"安个利用户",
			tishi:"用户名能修改的次数很少，谨慎填写",
			zhuce:false,
			phone:''
		}
	},
	onLoad:function(e){
		console.log(e.phone)
		this.phone=e.phone
	},
	methods: {
		login:function(){
			uni.showLoading({
				title: '正在注册'
			});
			console.log(this.name,this.dataSex,this.phone)
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/reg.php?type=reg', //仅为示例，并非真实接口地址。
				data: {
					username:this.name,
					gender:this.dataSex,
					phone:this.phone,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					console.log(res)
					uni.hideLoading();
					if(res.data.code=="1"){
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
						server.userinfo=res.data.data;
						server.cookie=res.header['Set-Cookie'];
						setTimeout(function () {
							uni.redirectTo({
								url: '../Home/Home'
							})
						}, 1500);
					}
				},
				fail:function(){
					console.log('失败')
					uni.hideLoading();
					uni.showToast({
						title: "未知原因注册失败",
						position:'bottom',
						icon:'none',
						duration:2000,
						mask:true
					});
				}
			});
		},
		sex:function(e){
			this.dataSex=e
			
			if(e=="1"){
				this.imgurl.man="../../static/10.png"
				this.imgurl.woman="../../static/21.png"
			}else{
				this.imgurl.man="../../static/11.png"
				this.imgurl.woman="../../static/20.png"
			}
		},
		inpuing:function(e){
			this.name=e.detail.value;
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/reg.php?type=checkName&name='+this.name, //仅为示例，并非真实接口地址。
				data: {
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					if(res.data.code=="0"){
						this.tishi=res.data.msg
						this.zhuce=false
					}else{
						this.tishi=res.data.msg
						this.zhuce=true
					}
					console.log(this.zhuce)
				}
			});
			
		}
	}
}
